<template>
    <div>
        <h1>קודם כל נבחר דמות</h1>
        <img id="girl" @click="chooseCharacter" :class="isGirlCelected ? 'celected-character': 'character'" class="girl" src="../assets/media/girlPlayer.svg">
        <img id="boy" @click="chooseCharacter"  :class="isBoyCelected ? 'celected-character': 'character'"  class=" boy" src="../assets/media/boyPlayer.svg">
        <button @click="movePage" class="instructions-button">!ועכשיו להוראות</button>
    </div>
</template>

<script>
export default {
    name: "chooseCharacter",
    data() {
        return {
            isGirlCelected: false,
            isBoyCelected: false,
            charactersSrc: ""
        };
    },
    methods: {
       chooseCharacter(event) {
           if (event.target.id === "girl") {
                this.isGirlCelected = true;
                this.isBoyCelected= false;
                this.charactersSrc = require("@/assets/media/girlPlayer.svg");
           }
           else {
                this.isGirlCelected = false;
                this.isBoyCelected= true;
                this.charactersSrc = require("@/assets/media/boyPlayer.svg");
           }
            this.$emit('celected-character', this.charactersSrc);  
       },
       movePage(event) {
           if(this.charactersSrc !== "") {               
                this.$emit('move-page', 2); 
           }
       }
    },
}
</script>

<style scoped>

.character {
    position: absolute;
    height: 43vh;
    width: 13vh;
    cursor: pointer;
    filter: brightness(0.5);
}

.celected-character {
    position: absolute;
    height: 43vh;
    width: 13vh;
    cursor: pointer;
    animation: celected 0.5s 1;
}

.boy {
    left: 13vw;
    top: 29vh;
}

.girl {
    left: 59vw;
    top: 29vh;
}

h1 {
    position: absolute;
    width: 41vh;
    text-align: center;
    top: 15vh;
    left: 5.5vw;
    font-size: 5vh;
}

button {
   position: absolute;
    background-color: rgb(200, 122, 105);
    border-radius: 1.5vh;
    border: rgb(200, 122, 105);
    color: white;
    cursor: pointer;
}

.instructions-button {
    top: 76vh;
    left: 27vw;
    height: 8vh;
    width: 45vw;
    font-size: 3vh;
}

@keyframes celected {
    50% {
        transform: translate(0, -3vh);
    }
    100% {
        transform: translate(0, 0);
    }
}
</style>