<template>
    <div>
        <div class="white-mask"></div>
        <div v-if="numPage === 1">
        <h1>ברוכים הבאים ללומדת תוצרי הדרכה מתוקשבת</h1>
        <button class="start-button" @click="movePage">!צאו לדרך</button>
        </div>
        <div v-else-if="numPage === 2" >
            <img class="computer" src="../assets/media/computer.png" alt="computer">
            <p class="text">מפתחת לומדה הגיעה לבית הספר לקוסמים ללמד אותם על תוצרי הדרכה מתוקשבת בשביל לעזור להם לשדרג את דרכי הלמידה שלהם. אך אבוי, המפתחת לומדה התבלבלה בדרכה לכיתה ונתקעה באחד המרתפים. 
            המשימה שלך ההוא לעזור למפתחת הלומדה לצאת מהמרתף.
            בשביל לעשות זאת עלייך לאסוף מפתחות בעזרת למידת סוגי ההדרכה השונים
            </p>
            <p class="good-luck">!בהצלחה</p>
            <button class="next-button " @click="movePage">אפשר לסמוך עלי</button>
        </div>
        <choose-character @move-page="moveToInstructions" @celected-character="celectedCharacter" v-else-if="numPage === 3"></choose-character>
    </div>
</template>

<script>
import ChooseCharacter from "@/components/ChooseCharacter";

export default {
    name: "openScreen ",
    components: {
        ChooseCharacter,
    },
data() {
        return {
            numPage: 1
        }
    },
    methods: {
        movePage() {
            this.numPage += 1
        },
        celectedCharacter(character) {
            this.$emit('celected-character', character);
        },
        moveToInstructions() {
            this.$emit('move-page', 2); 
        }
    },
    
}
</script>

