<template>
    <div >
        <div class="white-mask"></div>
        <img class="computer" src="../assets/media/computer.png" alt="computer">
            <button id="next" @click="changePage">{{btnName}}</button>
        <div v-if="page === 1">
            <p class="title-1"> השתמשו בחצי המקלדת כדי לזוז</p>
            <img class="arrow left" src="../assets/media/leftArrow.png" alt="left arrow">
            <img class="arrow right" src="../assets/media/rightArrow.png" alt="right arrow">
            <div class="left-right-move">
                <div class="cell" v-for="i in characterPlacement" :key="i">
                    <img  v-if="currentPlace === i" class="character" :src="character">
                </div>
            </div>
        </div>
        <div v-else-if="page === 2">
            <p class="title-2">השתמשו בחצים כדי לטפס ולרדת בסולם</p>
            <button id="back" @click="changePage">הקודם</button>
            <img class="up-down-arrow up" src="../assets/media/upArrow.png" alt="left arrow">
            <img class="up-down-arrow down" src="../assets/media/downArrow.png" alt="right arrow">
            <div class="ladder"></div>
            <div class="up-down-move">
                <div class="cell" v-for="i in characterPlacement" :key="i">
                    <img  v-if="currentPlace === i" class="character" :src="character">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "instructions ",
    props: ["character"],
    data() {
        return {
            btnName: "הבא",
            page: 1,
            characterPlacement: [],
        DIR_MAT: [
                [1, 0], 
                [0, 1],
                [-1, 0], 
                [0, -1]
            ],
            currentPlace: 10,
            myDir: "",
            DIR_DIFFERENCE: 40,
        };
    },
    methods: {
        changePage(event){
            if (event.target.id === "next"){
                this.currentPlace = 5
                if (this.page === 2){
                    this.page = 2
                    if (this.btnName === "!מתחילים") {
                    this.$emit("next-page", 3)
                }
    
                } else {
                     this.page += 1
                }
                this.btnName = "!מתחילים"
            }
            if (event.target.id === "back"){
                if (this.page === 1){
                    this.page = 1
                } else {
                     this.page -= 1
                    this.btnName = "!הבא"
                }
            }            
        }, 
        movecharacter(dir) {
            if (dir === 3) {
                if (this.currentPlace === 0) {
                    this.currentPlace = 0
                } else {
                    this.currentPlace -= 1
                }
            }
            if (dir === 1) {
                if (this.currentPlace === 13) {
                    this.currentPlace = 13
                } else {
                    this.currentPlace += 1
                }
            }
             if (dir === 0) {
                if (this.currentPlace === 5) {
                    this.currentPlace = 5
                } else {
                    this.currentPlace -= 1
                }
            }
            if (dir === 2) {
                if (this.currentPlace === 13) {
                    this.currentPlace = 13
                } else {
                    this.currentPlace += 1
                }
            }                   
        }
    },
     mounted() {         
        for (let i = 0; i < 14; i++) {
            this.characterPlacement.push(i);
        } 
    window.addEventListener("keydown", event => {
          this.myDir = this.DIR_DIFFERENCE - event.keyCode;
            if (this.page === 1) {
                if ((this.myDir === 3) || (this.myDir === 1)) {
                    this.movecharacter(this.myDir);
                }
            }
            if (this.page === 2) {
                if ((this.myDir === 0) || (this.myDir === 2)) {
                    this.movecharacter(this.myDir);
                }
            }
    });
  }
}
</script>

<style scoped>
.white-mask {
    background-color: rgba(255, 255, 255, 0.6);
    height: 100vh;
    width: 100vw;
    position: absolute;
}
.ladder {
    background-image: url("../assets/media/ladder.png");
    height: 58vh;
    width: 25vw;
    position: absolute;
    top: 29vh;
    left: 37.5vw;
    background-repeat: repeat;
}

.computer {
    position: absolute;
    left: 1.5vw;
    top: 0;
    height: 98vh;
    width: 97vw;
}

button {
    background-color: rgb(200, 122, 105);
    position: absolute;
    top: 80vh;
    height: 6vh;
    width: 20vw;
    font-size: 2vh;
    color: white;
    cursor: pointer;
    z-index: 6;
    border-radius: 1.5vh;
    border: rgb(200, 122, 105);
}

#back {
    left: 68vw;
}

#next {
    left: 12vw;
}
p {
    position: absolute;
    font-size: 4vh;
    text-align: center
}

.title-1 {
    left: 13.5vw;
    top: 9vh;
    width: 75vw;
}

.title-2 {
    left: 14vw;
    top: 9vh;
    width: 71vw;
}

.arrow {
    position: absolute;
    top: 23vh;
    height: 13vh;
    width: 14vw;
}

.left {
    left: 34vw;
}

.right {
    left: 54vw;
}

.up-down-arrow {
    position: absolute;
    left: 32vh;
    height: 10vh;
    width: 13vw;
}

.up {
    top: 37vh;
}

.down {
    top: 53vh;
}
.character {
    /* top: 50%;
    left: 50%; */
    height: 25vh;
    width: 16vh;
}

.left-right-move {
    height: 5vh;
    width: 53vw;
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 41vh;
    left: 22vw;
    margin: 2vw;
}

.up-down-move {
    height: 60vh;
    width: 46vw;
    display: flex;
    flex-direction: column-reverse;
    position: absolute;
    top: 26vh;
    left: 29vw;
    margin: 2vw;
}

.cell {
    height: 5vh;
    width: 62vw;
}
</style>