<template>
    <div>
        <p v-if="screenNear !== ''" class="help">לחצו על רווח כדי לפתוח את המסך</p>
        <p v-else-if="doorNear === 'end-game' && finishedScreens.length !== 8" class="help">עליך למצוא את כל המפתחות כדי לפתוח את הדלת האחרונה</p>
        <p v-else-if="doorNear !== ''" class="help">לחצו על רווח כדי לעבור בדלת</p>
        <p v-else-if="finishedScreens.length === 8" class="help">מצאת את כל המפתחות! עכשיו אפשר לפתוח את הדלת האחרונה</p>

        <div class="row" v-for="i in 45" :key="i">
            <div class="cell" v-for="j in 18" :key="j">
                {{i}}
                <img v-if="i === playerRow - 1 && j === playerCol - 1 " :src="character" alt="player" class="player">
            </div>
        </div>
        <div class="floor" id="ground"></div>
        <div v-if="level === 1">
            <img src="../assets/media/door.svg" alt="to level 2" class="door" id="to-level2">
            <div class="ladder" id="l1"></div>
            <div class="floor" id="f1"></div>
        </div>
        <div v-else>
            <img src="../assets/media/door.svg" alt="to level 1" class="door" id="to-level1">
            <img src="../assets/media/door.svg" alt="end game" class="door" id="end-game">
            <div class="ladder" id="l2"></div>
            <div class="ladder" id="l3"></div>
            <div class="ladder" id="l4"></div>

            <div class="floor" id="f2"></div>
            <div class="floor" id="f3"></div>
            <div class="floor" id="f4"></div>

        </div>
        <screens :level="level" :finishedScreens="finishedScreens"></screens>
        <teaching-products @close="finishedLearning" @close-info="closeScreen" v-if="isLearning && level===2" :screen="screenNear"></teaching-products>
        <interactivity :screen="screenNear" @finish-screen="finishedLearning" @close-screen="closeScreen"  v-if="isLearning && level===1"></interactivity>
        <keys :numOfKey="keys"></keys>
    </div>
</template>
<script>
import Screens from "@/components/Screens.vue";
import TeachingProducts from "@/components/TeachingProducts.vue";
import Interactivity from "@/components/Interactivity.vue";
import Keys from "@/components/Keys.vue";



import { finished } from 'stream';
// import { log } from 'console';
export default {
    name: "board",
    props: ["level", "levelArr", "character", "keys"],
    components: {
        Screens,
        TeachingProducts,
        Interactivity,
        Keys
    },
    data() {
        return {
            playerRow: 41,
            playerCol: 4,
            DIR_MAT: [[1, 0], //Dowm
            [0, 1], //Right
            [-1, 0], //Up
            [0, -1]], //Left
            screenNear: '',
            doorNear: '',
            doors: ["to-level2","to-level1","end-game"],
            isLearning: false,
            screens: ["interactivity", "interactivity-levels", "infographics", "web-product",
             "animation", "video", "educational-game", "tutorial"],
            finishedScreens: [],
        }
    },
    methods: {
        moveHorizontal(dir) {
            let direction = this.DIR_MAT[dir][1];            
            let cellUnder = this.levelArr[this.playerRow + 1][this.playerCol];
            let nextCell =  this.levelArr[this.playerRow][this.playerCol + direction];
            let nextCellUnder = this.levelArr[this.playerRow + 1][this.playerCol + direction];
            let currentCell =  this.levelArr[this.playerRow][this.playerCol];
            if (cellUnder === 'floor' && nextCell !== 'block' && nextCellUnder !== 'empty' || cellUnder === 'ladder' && currentCell !== 'ladder') {
                this.playerCol += direction;
                this.isCloseToScreen();
                this.isCloseToDoor();
            }
        },

        moveVertical(dir) {
            let direction = this.DIR_MAT[dir][0];
            let nextCell =  this.levelArr[this.playerRow + direction][this.playerCol];
            let currentCell =  this.levelArr[this.playerRow][this.playerCol];
            if (currentCell === 'ladder' && (nextCell === 'ladder' || nextCell === 'empty') || currentCell === 'empty' && nextCell === 'ladder') {
                this.playerRow += direction;
            }
        },
        pressedSpace() {
            if (this.screenNear !== '') {
                this.isLearning = true;
4            }
            else if (this.doorNear !== '') {
                if (this.playerCol < 10) {
                    this.playerCol = 18;
                }                  
                else {
                    this.playerCol = 4;
                }
                this.$emit(this.doorNear);
            }
        },

        isCloseToDoor() {
            if (this.doors.includes(this.levelArr[this.playerRow][this.playerCol])) {
                this.doorNear = this.levelArr[this.playerRow][this.playerCol];               
            }
            else {
                this.doorNear = '';
            }
        },

        isCloseToScreen() {
            for (let i = this.playerCol - 2; i < this.playerCol + 2; i++) {
                if (this.screens.includes(this.levelArr[this.playerRow][i])) {
                    this.screenNear = this.levelArr[this.playerRow][i];
                    return;
                }
                else {
                    this.screenNear = '';
                }
            }  
        },

        closeScreen() {
             this.isLearning = false;
        },

        finishedLearning() {
            this.isLearning = false;
            if (!this.finishedScreens.includes(this.screenNear)){
                this.finishedScreens.push(this.screenNear);
                this.$emit("found-key");                
            }
            this.isLearning = false;
        },
        
    },
    mounted() {
        if (!this.isLearning) {
            window.addEventListener("keydown", (event) => {
                let direction = 40 - event.keyCode;
                if (direction === 0 || direction === 2) {
                    this.moveVertical(direction);
                    
                }
                else if (direction === 1 || direction === 3) {
                    this.moveHorizontal(direction);
                }    
                else if (event.code === 'Space') {
                    this.pressedSpace();
                }
            });
        }
    },
}
</script>
<style scoped>
    .row {
        height: 2.3vh;
    }

    .cell {
        display: inline-block;
        width: 5vw;
        margin: 0;
        color: rgba(240, 248, 255, 0);
        user-select: none;
        border: 1px solid black;
    }

    .help {
        text-align: center;
        width: 60vw;
        position: absolute;
        left: 20vw;
        font-size: 2rem;
    }

    .player {
        z-index: 5;
        transform: translate(0, -70%);
        position: absolute;
        /* background-color: orange; */
        /* opacity: 0.5; */
        width: 8vw;
        height: 20vh;
    }

    .door {
        height: 22vh;
        width: 10vw;
        position: absolute;
        bottom: 9.8vh;
    }

    #to-level2 ,
    #end-game {
        right: 2vw;
    }

    #to-level1 {
        left: 2vw;
    }

    .floor {
        background-image: url('../assets/media/bricks.png');
        background-size: 100% unset;
        height: 8vh;
        position: absolute;
        border-radius: 0.5vw;
        /* opacity: 0.5; */
    }

    #ground.floor {
        border-radius: 0;
        width: 100vw;
        height: 10vh;
        bottom: 0vh;
        left: 0; 
    }

    #f1.floor {
        width: 60vw;
        bottom: 58vh;
        left: 20vw; 
    }

    #f2.floor {
        width: 50vw;
        bottom: 36vh;
        left: 40vw; 
    }

    #f3.floor {
        width: 30vw;
        bottom: 58vh;
        left: 0vw; 
    }

    #f4.floor {
        width: 44vw;
        bottom: 64vh;
        left: 52vw; 
    }

    .ladder {
        z-index: 4;
        background-image: url('../assets/media/ladder.svg');
        background-size: 100% auto;
        position: absolute;
        width: 6vw;
        /* opacity: 0.5; */
    }

    #l1.ladder {
        bottom: 10vh;
        left: 52vw;
        height: 56vh
    }

    #l2.ladder {
        bottom: 10vh;
        left: 58vw;
        height: 34vh
    }

    #l3.ladder {
        bottom: 10vh;
        left: 22vw;
        height: 56vh
    }

    #l4.ladder {
        bottom: 44vh;
        left: 74vw;
        height: 28vh
    }
</style>