<template>
    <div class="levels">
            <div v-if="level === 1">
                <board :level="level"
                 :levelArr="level1" 
                 :character="character" 
                 @to-level2="toLevel2"
                 @to-level1="toLevel1"
                 @end-game="endGame"
                 @found-key="keys ++"
                 :keys="keys">
                 </board>
            </div>

            <div v-else>
                <board :level="level" 
                :levelArr="level2" 
                :character="character"
                @to-level2="toLevel2"
                @to-level1="toLevel1"
                @end-game="endGame"
                @found-key="keys ++"
                :keys="keys">
                </board>
            </div>
        </div>
</template>
<script>
import Board from "@/components/Board.vue";


export default {
    //block*2 from the sidse 
    //ladder
    //floor
    name: "levels",
    props: ["character"],
    components: {
        Board,
    },
    data() {
        return {
            level1: [],
            level2: [],
            level: 1,
            keys: 0,
            startColumn: 4
        };
    },

    methods: {
        restartMatrix(matrix) {
            for (let i = 0; i < 46; i++) {
                matrix.push([]);
                for (let j = 0; j < 20; j++) {
                    if (j == 0 && j <= 1 || j >= 19 && j <= 20 ) {
                        matrix[i].push('block');
                    }
                    else {
                        matrix[i].push('empty');
                    }
                }   
            }
        },

        setGround(matrix) {
            for (let i = 45; i > 41; i--) {
                for (let j = 2; j < 19; j++) {
                    matrix[i][j] = 'floor';
                }
            }
        },

        setFloors(matrix, startCol, endCol, startRow) {
            for (let i = startRow; i < startRow + 4; i++) {
                for (let j = startCol; j < endCol; j++) {
                    matrix[i][j] = 'floor';
                }
            }
        },

        setLadders(matrix, col, startRow, endRow) {
            for (let i = startRow; i < endRow; i++) {
                matrix[i][col] = 'ladder';
            }
        },

        setScreens(matrix, row, startCol, screenID) {
            for (let i = startCol; i < startCol + 5; i++) {
                matrix[row][i] = screenID;
            }
        },

        setDoors(matrix, startCol, doorID) {
            for (let i = startCol; i < startCol + 5; i++) {
                matrix[44][i] = doorID;
            }
        },

        buildLevel1() {
            this.restartMatrix(this.level1);
            this.setGround(this.level1);

            this.setFloors(this.level1 ,10, 40, 17);
            this.setLadders(this.level1 , 10, 10, 40);

            this.setScreens(this.level1, 44, 15, 'interactivity');
            this.setScreens(this.level1, 16, 33, 'interactivity-levels');

            this.setDoors(this.level1, 44, 'to-level2')
        },

        buildLevel2() {
            this.restartMatrix(this.level2);
            this.setGround(this.level2);

            this.setFloors(this.level2 , 26 ,48 ,14);
            this.setFloors(this.level2 , 0 ,15 ,17);
            this.setFloors(this.level2 , 20, 45, 28);

            this.setLadders(this.level2 , 12, 17, 45);
            this.setLadders(this.level2 , 30, 28, 45);
            this.setLadders(this.level2 , 38, 14, 28);

            this.setScreens(this.level2 , 16, 2, 'educational-game');
            this.setScreens(this.level2 , 44, 16, 'tutorial');
            this.setScreens(this.level2 , 27, 23, 'infographics');
            this.setScreens(this.level2 , 44, 35, 'video');
            this.setScreens(this.level2 , 13, 30, 'animation');
            this.setScreens(this.level2 , 13, 42, 'web-product');

            this.setDoors(this.level2, 1, 'to-level1');
            this.setDoors(this.level2, 44, 'end-game');
        },

        toLevel1() {
           this.level = 1;
            this.startColumn = 46; 
        },

        toLevel2() {
            this.level = 2;
            this.startColumn = 6; 
        },

        endGame() {
            if (this.keys === 8) {
            this.$emit("end-screen", 4);

            }
        }
    },

     mounted() {
    this.buildLevel1();
    this.buildLevel2();    
  } 
}
</script>
<style scoped>
    .levels {
        height: 100vh;
        width: 100vw;
        position: absolute;
        top: 0;
        left: 0;
    }
</style>